import React, { useState } from "react";
import { useTranslation } from "react-i18next";

function LoadMoreButton({ pages, fetchMoreData }) {
	const [loading, setLoading] = useState(false);

	if (loading) {
		setTimeout(() => {
			setLoading(false);
		}, 2000);
	}
	const { t } = useTranslation();
	return (
		<div
			className={
				"load-more-container text-center" +
				(pages.variations.length > 11 ? "" : " d-none")
			}
		>
			{/* {pages.more_load_time ? ( */}
			<button
				className="btn btn-outline-darker btn-load-more"
				onClick={() => {
					setLoading(true);
					fetchMoreData(true);
				}}
			>
				<span>{t("load-more")}</span>
				{loading ? (
					<i className="icon-refresh load-more-rotating"></i>
				) : (
					<i className="icon-refresh"></i>
				)}
			</button>
			{/* ) : null} */}
		</div>
	);
}

export default LoadMoreButton;
