import React from "react";
import { useSelector } from "react-redux";
import Banner from "./Banner";

function Banners({ block, position }) {
	const banners = useSelector(state => state.any.banners) || [];
	// console.log(banners);

	return (
		<div className="banners">
			{block
				? block[0]?.photos?.map((item, index) => {
						return <Banner item={item} key={index} />;
				  })
				: banners
						.filter(banner => banner.position === position)
						.map((item, index) => {
							return (
								<Banner
									item={item}
									key={index}
									size={"800x_"}
								/>
							);
						})}
		</div>
	);
}

export default Banners;
