import React, { useState } from "react";
import { Link } from "react-router-dom";
import LoaderOverlay from "../../../../components/features/loaders/LoaderOverlay";
import BlogItem from "./BlogItem";
import { useSelector } from "react-redux";
import StoryPopup from "./StoryPopup";
import { debounce } from "../../../../utils/debounce";
import OwlCarousel from "../../../../components/features/owl-carousel";
import { topBrandSlider2 } from "../../../../components/options";

export default function BlogSection() {
	const blogs = useSelector(state => state.any.blogs);
	const [currentPost, setCurrentPost] = useState();
	const [open, setOpen] = useState(false);
	const [loading, setLoading] = useState(true);

	function changerStory(prev) {
		if (
			parseInt(currentPost.id) === parseInt(blogs[currentPost.index].id)
		) {
			setCurrentPost(
				prev
					? blogs[currentPost.index - 1]
						? blogs[currentPost.index - 1]
						: blogs[currentPost.index]
					: blogs[currentPost.index + 1]
					? blogs[currentPost.index + 1]
					: setOpen(false)
			);
			setLoading(true);
		}
	}

	let set = index => {
		debounce(
			() => {
				if (blogs[index + 1] && open) {
					setCurrentPost(blogs[index + 1]);
				} else {
					setOpen(false);
				}
				setLoading(true);
			},
			"index",
			8000
		);
	};

	if (!blogs) {
		return <LoaderOverlay />;
	}

	return (
		<div className="blog-posts blog-posts-v2 no-on-mobile">
			{open && (
				<StoryPopup
					open={open}
					loading={loading}
					setLoading={setLoading}
					setOpen={setOpen}
					currentPost={currentPost}
					changerStory={changerStory}
					set={set}
				/>
			)}
			<OwlCarousel
				adClass={`owl-simple brand-carousel`}
				carouselOptions={topBrandSlider2}
				carouselId="brandslider"
			>
				{blogs.map(
					(item, index) =>
						item.image && (
							<li
								className={
									item.hidden
										? "blog no-active_blog"
										: "blog active_blog"
								}
								key={index}
								style={{ margin: "10px 0" }}
							>
								<BlogItem
									setOpen={setOpen}
									currentPost={currentPost}
									setCurrentPost={setCurrentPost}
									post={item}
									key={index}
								/>
							</li>
						)
				)}
			</OwlCarousel>

			<div className="more-container text-center mt-2">
				<Link to="/blog" className="btn btn-outline-darker btn-more">
					<span>View more articles</span>
					<i className="icon-long-arrow-right"></i>
				</Link>
			</div>
		</div>
	);
}
