import React, { useState } from "react";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { hiddenBlog } from "../../../../actions/any";
import { useTranslation } from "react-i18next";
import LoaderOverlay from "../../../../components/features/loaders/LoaderOverlay";
import { getImageUrl } from "../../../../utils/oxMethods";

const StoryPopup = ({
	open,
	setOpen,
	currentPost,
	changerStory,
	set,
	loading,
	setLoading
}) => {
	const { t } = useTranslation();
	const [currentId, setCurrentId] = useState();
	const dispatch = useDispatch();
	const esCape = 27;
	let loadImg = 0;

	function onLoad() {
		if (currentPost?.id) {
			loadImg = setTimeout(() => {
				set(currentPost.index);
				setLoading(false);
			}, 500);
		}
	}

	useEffect(() => {
		const fn = e => {
			if (e.keyCode === esCape) {
				setOpen(false);
			}
		};
		window.addEventListener("keyup", fn);
		return () => {
			clearTimeout(loadImg);
			window.removeEventListener("keyup", fn);
		};
	}, []);

	useEffect(() => {
		let time = 0;
		setCurrentId(0);
		if (currentPost?.id) {
			time = setTimeout(() => {
				setCurrentId(currentPost.id);
				setLoading(true);
			}, 100);
			dispatch(hiddenBlog(currentPost.id));
		}
		return () => {
			clearTimeout(time);
		};
	}, [currentPost?.id]);

	return (
		<div
			className="story_modal_container"
			style={{ visibility: open ? "visible" : "hidden" }}
		>
			<div className={`modal_content ${open ? "popUpModal" : ""}`}>
				{loading ? (
					<div className="story-loading">
						<LoaderOverlay />
						<img
							onLoad={onLoad}
							className="d-none"
							src={getImageUrl(currentPost?.image, "800x_")}
							alt="##"
						/>
					</div>
				) : (
					<img
						className="story-image"
						src={getImageUrl(currentPost?.image, "800x_")}
						alt={
							"##"
							// currentPost?.image.originalName
							// 	? currentPost?.image.originalName
							// 	: ""
						}
					/>
				)}
				<span
					className={`time-story ${
						!loading && currentId ? "active" : ""
					}`}
				>
					<span></span>
				</span>
				<i
					className="icon-close bx bx-x bx-md close_btn"
					onClick={() => setOpen(false)}
				></i>
				<h2 className="story_title">{currentPost?.title}</h2>
				<Link
					to={`blog/${currentPost?.id}`}
					className="stories-btn-block w-100"
				>
					<button className="btn-product">
						<span>{t("more")}</span>
					</button>
				</Link>
				<button
					onClick={() => {
						return changerStory(true); //  setLoading(true);
					}}
					className="story-btn prev"
				></button>
				<button
					onClick={() => {
						return changerStory(false); // setLoading(true);
					}}
					className="story-btn next"
				></button>
			</div>
		</div>
	);
};

export default StoryPopup;
