import React, { useState, useRef } from "react";
import ProductListThree from "../../../../components/features/product/details/ProductListThree";
import { useTranslation } from "react-i18next";

function RecommendedCollection({ collection }) {
	const { t } = useTranslation();

	const queryString = require("qs");
	const initialQuery = { page: 1, size: 12 };

	const [query, setQuery] = useState(
		window.location.search.length > 0
			? queryString.parse(window.location.search.slice(1))
			: { ...initialQuery }
	);

	const myRef = useRef(null);
	const executeScroll = () =>
		myRef.current.scrollIntoView({ behavior: "smooth" });

	return (
		<div className="new_product_wrapper">
			<div className="products_list">
				<h2
					style={{ position: "absolute", top: "3px" }}
					className="title new_products_title mb-0"
				>
					{t("recomended-items")}
				</h2>
				{collection ? (
					<ProductListThree
						query={query}
						setQuery={setQuery}
						menu={collection}
						column="4cols"
						executeScroll={executeScroll}
					/>
				) : null}
			</div>
		</div>
	);
}

export default RecommendedCollection;
