import React from "react";
import { Link } from "react-router-dom";
import { getImageUrl } from "../../../../utils/oxMethods";
import { extractTextFromHTML, sliceByWord } from "../../../../utils/string";

function BlogItem(props) {
	const { post, setOpen, setCurrentPost } = props;
	let date = new Date(post.createdTime);
	let options = {
		year: "numeric",
		month: "short",
		day: "2-digit",
		timeZone: "UTC"
	};

	return (
		<article className="entry">
			<figure className="entry-media">
				<div className="lazy-overlay bg-3">
					<Link
						to={"#"}
						onClick={() => {
							setCurrentPost(post);
							setOpen(true);
						}}
					>
						<img
							src={getImageUrl(post.image, "100x_")}
							alt="post"
							width={100}
							threshold={300}
							effect="blur"
						/>
					</Link>
				</div>
			</figure>

			<div className="entry-body text-center">
				<div className="entry-meta">
					<Link to={`blog/${post.id}`}>
						{date.toLocaleDateString("en-US", options)}
					</Link>
				</div>

				<h3 className="entry-title">
					<a href={`${process.env.PUBLIC_URL}/blog/${post.id}`}>
						{post.title}
					</a>
				</h3>

				<div className="entry-content">
					<p
						className="pl-1 pr-1"
						dangerouslySetInnerHTML={{
							__html: sliceByWord(
								extractTextFromHTML(post.text),
								0,
								20
							)
						}}
					></p>

					<a
						href={`${process.env.PUBLIC_URL}/blog/${post.id}`}
						className="read-more"
					>
						Read More
					</a>
				</div>
			</div>
		</article>
	);
}

export default BlogItem;
