import React, { useState, useEffect, Fragment } from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import { setStatePages } from "../../../../actions/any";
import ProductCard from "..";
import QuickView from "../QuickViewNew";
import { addToCart } from "../../../../actions/cart";
import { getProductsPromotions } from "../../../../api/products";
import Toolbox from "./toolbox";
import SimpleShimmerProducts from "../../loaders/SimpleShimmerProducts";
import ChooseVariationModal from "../../modal/choose-variation";
import LoadMoreButton from "./loadMoreButton";
import { useTranslation } from "react-i18next";
import { getImageUrl } from "../../../../utils/oxMethods";
import { useDispatch } from "react-redux";
import { getCollectionProducts } from "../../../../api/collections";
import Pagination from "./Pagination";
import market from "../../../../config/market";

const MORE_LOAD_PAGE_STEP = 3;

// const grid = {
// 	"2cols": "col-6",
// 	"3cols": "col-6 col-md-4 col-lg-4",
// 	"4cols": "col-4 col-md-4 col-lg-4 col-xl-3 p-0",
// 	"5cols": "col-6 col-md-4 col-lg-3 col-xl-5 col"
// };
const queryString = require("qs");
const initialPages = {
	page: 1,
	totalCount: 0,
	variations: [],
	hasMore: true,
	allPages: 1
};
function ProductListThree({ menu, query, setQuery, column }) {
	const dispatch = useDispatch();
	const { t } = useTranslation();
	const [cols, setCols] = useState(column);
	const [loading, setLoading] = useState(true);
	const [pages, setPages] = useState(initialPages);
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 0 } }
	});
	function toTop() {
		window.scroll({
			top: 0
		});
	}
	let mount = true;
	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct
		});
	}

	async function getProducts(query, more) {
		const strQuery = queryString.stringify(query);
		getCollectionProducts(menu.id, 0, strQuery)
			.then(async page => {
				if (mount) {
					setPages(prevPage => ({
						page: page.page,
						totalCount: page.totalCount,
						variations: more
							? [...prevPage.variations, ...page.variations]
							: page.variations,

						hasMore: page.variations.length > 0,
						allPages: Math.ceil(page.totalCount / query.size)
					}));
					setLoading(false);

					const ids = page.variations.reduce((pv, nv) => {
						return [...pv, nv.variation.id];
					}, []);

					let products = more ? pages.variations : [];
					if (ids.length) {
						await getProductsPromotions(ids).then(res => {
							if (res.length) {
								page.variations.forEach(variation => {
									products.push({
										...variation,
										...res.find(
											v =>
												v.variationId ===
												variation.variation.id
										)
									});
								});
							}
						});
						setPages(prevPage => ({
							...prevPage,
							variations: products
						}));
					}
				}
			})
			.catch(err => {
				setPages(prevPage => ({
					...prevPage,
					hasMore: false
				}));
				setLoading(false);
			});
	}

	function fetchMoreData(load_more = false) {
		if (pages.totalCount === pages.variations.length) {
			setPages(prevPage => ({
				...prevPage,
				more_load_time: false,
				hasMore: false
			}));
			return;
		}
		if (!load_more && pages.page % MORE_LOAD_PAGE_STEP === 0) {
			setPages(prevPage => ({
				...prevPage,
				hasMore: false,
				more_load_time: true
			}));
			return;
		}
		query.page = parseInt(query.page) + 1;
		getProducts(query, true);
	}

	useEffect(() => {
		setLoading(true);
		getProducts(query, false);
		toTop();
		return () => {
			mount = false;
		};
	}, [query, menu.id]);

	useEffect(() => {
		if (pages.variations.length > 0) {
			let p = [];
			pages.variations.map(page =>
				p.push({
					id: page.variation.id,
					name: page.variation.name,
					image: getImageUrl(page.variation.images[0], "300x_")
				})
			);
			dispatch(setStatePages(p));
		}
	}, [pages.variations]);
	return (
		<>
			<Toolbox
				setCols={setCols}
				setQuery={setQuery}
				cols={cols}
				pages={pages}
			/>
			{loading ? (
				<SimpleShimmerProducts size={4} />
			) : (
				<div className="products mb-3">
					{pages.variations.length === 0 ? (
						<div className="row">
							<h4 className="ml-3">{t("nothing-found")}</h4>
						</div>
					) : (
						<Fragment>
							<InfiniteScroll
								dataLength={pages.variations.length}
								next={fetchMoreData}
								hasMore={pages.hasMore}
								loader={
									<div className="w-100">
										<SimpleShimmerProducts size={4} />
									</div>
								}
								className="row"
							>
								{pages.variations.map(product => (
									<div
										className={`col-6 col-md-4 col-lg-3 d-flex`}
										key={product.variation.id}
									>
										<ProductCard
											product={product}
											type="sidebar"
											onAddToCart={dataProduct => {
												onAddToCart(dataProduct);
											}}
										/>
									</div>
								))}
							</InfiniteScroll>
							{pages.allPages > 1 && market.config.home !== 5 ? (
								<>
									<LoadMoreButton
										pages={pages}
										fetchMoreData={fetchMoreData}
									/>
									<div className="page-pagination">
										<Pagination
											pageCount={pages.allPages}
											onChange={({ selected }) => {
												setQuery(prev => ({
													...prev,
													page: 1 + selected
												}));
											}}
											forcePage={query.page}
										/>
									</div>
								</>
							) : null}
							<QuickView />
							<ChooseVariationModal
								setModal={setModal}
								modal={modal}
								addToCart={addToCart}
							/>
						</Fragment>
					)}
				</div>
			)}
		</>
	);
}

export default ProductListThree;
