import React from "react";
import { useTranslation } from "react-i18next";

function Toolbox({ setCols, pages, cols, setQuery }) {
	function changeFilter(e) {
		setQuery(prevQ => ({
			...prevQ,
			order: e.target.value
		}));
	}
	const { t } = useTranslation();
	return (
		<div className="toolbox">
			<div className="toolbox-center">
				<div className="toolbox-info">
					{t("shown")}{" "}
					<span>
						{pages.variations.length} {t("from")} {pages.totalCount}
					</span>{" "}
					{t("products")}
				</div>
			</div>

			<div className="toolbox-right">
				<div className="toolbox-sort">
					<label htmlFor="sortby">{t("sort")}:</label>
					<div className="select-custom">
						<select
							name="order"
							id="sortby"
							onChange={changeFilter}
							className="form-control"
						>
							<option value="name asc">{t("titleAZ")}</option>
							<option value="name desc">{t("titleZA")}</option>
							<option value="sellPrice asc">
								{t("cheaper")}
							</option>
							<option value="sellPrice desc">
								{t("expensive")}
							</option>
						</select>
					</div>
				</div>
			</div>

			<div className="toolbox-layout">
				<button
					className={`btn-layout ${
						"12cols" === cols ? "active" : ""
					}`}
					onClick={e => {
						setCols("12cols");
					}}
				>
					<svg width="16" height="10">
						<rect x="0" y="0" width="4" height="4"></rect>
						<rect x="6" y="0" width="10" height="4"></rect>
						<rect x="0" y="6" width="4" height="4"></rect>
						<rect x="6" y="6" width="10" height="4"></rect>
					</svg>
				</button>
				<button
					className={`btn-layout ${"2cols" === cols ? "active" : ""}`}
					onClick={e => {
						setCols("2cols");
					}}
				>
					<svg width="10" height="10">
						<rect x="0" y="0" width="4" height="4"></rect>
						<rect x="6" y="0" width="4" height="4"></rect>
						<rect x="0" y="6" width="4" height="4"></rect>
						<rect x="6" y="6" width="4" height="4"></rect>
					</svg>
				</button>

				<button
					className={`btn-layout ${"3cols" === cols ? "active" : ""}`}
					onClick={e => {
						setCols("3cols");
					}}
				>
					<svg width="16" height="10">
						<rect x="0" y="0" width="4" height="4"></rect>
						<rect x="6" y="0" width="4" height="4"></rect>
						<rect x="12" y="0" width="4" height="4"></rect>
						<rect x="0" y="6" width="4" height="4"></rect>
						<rect x="6" y="6" width="4" height="4"></rect>
						<rect x="12" y="6" width="4" height="4"></rect>
					</svg>
				</button>

				<button
					className={`btn-layout ${"4cols" === cols ? "active" : ""}`}
					onClick={e => {
						setCols("4cols");
					}}
				>
					<svg width="22" height="10">
						<rect x="0" y="0" width="4" height="4"></rect>
						<rect x="6" y="0" width="4" height="4"></rect>
						<rect x="12" y="0" width="4" height="4"></rect>
						<rect x="18" y="0" width="4" height="4"></rect>
						<rect x="0" y="6" width="4" height="4"></rect>
						<rect x="6" y="6" width="4" height="4"></rect>
						<rect x="12" y="6" width="4" height="4"></rect>
						<rect x="18" y="6" width="4" height="4"></rect>
					</svg>
				</button>
			</div>
		</div>
	);
}

export default Toolbox;
