import React, {
	useMemo
	//  useState
} from "react";
import { Helmet } from "react-helmet";
import { useSelector } from "react-redux";
import Slider from "../components/banners/Slider";
import Banners from "../components/banners/Banners";
import BlockNewsWidthCollection from "../components/blocks/BlockNewsWidthCollection";
// import { useTranslation } from "react-i18next";
// import { Link } from "react-router-dom";
import { getImageUrl } from "../../../utils/oxMethods";
import RecommendedCollection from "../components/collections/RecommendedCollection";
import BlogSection from "../components/blogs/BlogSectionTwo";
import MiniSlider from "../components/banners/MiniSlider";
import { dt } from "../../../i18n";

function HomePageDefault() {
	const marketData = useSelector(state => state.marketData);
	const { blocks } = useSelector(state => state.blocks);
	// const [showText, setShowText] = useState(false);
	// const description = marketData?.settings?.seoHtml;
	// const { t } = useTranslation();

	const latestCollection = blocks?.find(block => block.title === "latest");
	const miniCollection = blocks?.find(block => block.title === "mini_block");
	const recommendedCollection = blocks?.find(
		block => block.title === "recommended"
	);
	const topSellingCollection = blocks?.find(
		block => block.title === "top_selling"
	);
	const head = useMemo(() => {
		return {
			title: marketData.name,
			description: marketData.settings && marketData.settings.description,
			keyword: marketData.settings && marketData.settings.keyword,
			favicon: getImageUrl(marketData.settings.faviconImage, "50x_")
		};
	}, []);

	return (
		<>
			<Helmet>
				<title>{head.title}</title>
				<meta name="description" content={head.description} />
				<meta name="keywords" content={head.keyword} />
				<meta name="apple-mobile-web-app-title" content={head.title} />
				<meta name="application-name" content={head.title} />
				<link
					rel="icon"
					type="image/png"
					href={head.favicon}
					sizes="32x32"
				/>
			</Helmet>

			<div className="home-page-five">
				<div className="history only-mobile">
					<div className="container">
						<BlogSection />
					</div>
				</div>
				<div className="slider">
					<div className="container">
						<Slider />
					</div>
				</div>
				<div className="banner">
					<div className="container">
						<Banners position="top" />
					</div>
				</div>
				<div className="mini-collection">
					<div className="container">
						<div className="row">
							<div className="col-12">
								<h2 className="title">
									{dt(miniCollection?.name)}
								</h2>
							</div>
						</div>
						<MiniSlider block={miniCollection} />
					</div>
				</div>
				<div className="latest">
					<div className="container">
						<BlockNewsWidthCollection
							collection={latestCollection}
							title={"new-items"}
						/>
					</div>
				</div>
				<div className="top-selling">
					<div className="container">
						<BlockNewsWidthCollection
							collection={topSellingCollection}
							title={"best-seller"}
						/>
					</div>
				</div>

				<div className="recommended">
					<div className="container">
						<RecommendedCollection
							collection={recommendedCollection}
						/>
					</div>
				</div>
				{/* {description ? (
					<div className="container mb-4">
						<div
							className={`before-footer-text ${
								showText ? "active" : ""
							}`}
							dangerouslySetInnerHTML={{ __html: description }}
						/>
						<Link
							to="#"
							className={`opener ${
								showText ? "d-none" : "d-inline-block"
							}`}
							onClick={() => setShowText(!showText)}
						>
							{t("read-more")}
						</Link>
					</div>
				) : null} */}
			</div>
		</>
	);
}

export default HomePageDefault;
