import { Splide, SplideSlide } from "@splidejs/react-splide";
import QueryString from "qs";
import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { addToCart } from "../../../../actions/cart";
import { getProductsPage } from "../../../../api/products";
import ShimmerProducts from "../../../../components/features/loaders/ShimmerProducts";
import ChooseVariationModal from "../../../../components/features/modal/choose-variation";
import ProductCard from "../../../../components/features/product";
import { collectionFour } from "../../../../components/options";
import market from "../../../../config/market";
import Banner from "../banners/Banner";

export default function BlockNewsWidthCollection({ collection, title }) {
	const { menuList } = useSelector(state => state.menu);
	const [menu, setMenu] = useState([]);
	const [active, setActive] = useState(0);
	const { t } = useTranslation();
	const [products, setProducts] = useState([]);
	const [loading, setLoading] = useState(false);

	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 0 } }
	});

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct
		});
	}

	useEffect(() => {
		if (collection) {
			setLoading(true);
			if (collection.products) {
				setProducts(collection.products);
				setLoading(false);
			}
			if (
				menuList &&
				menuList.length > 0 &&
				collection.menus.length > 0
			) {
				const menus = collection.menus.map(m => {
					return (m = menuList.find(
						menu => menu?.id === parseInt(m)
					));
				});
				setMenu(menus);
				setActive(menus[0]?.id);
				setLoading(false);
			}
		}
	}, []);

	useEffect(() => {
		if (active) {
			setLoading(true);
			getProductsPage(
				active,
				QueryString.stringify({ size: 10, page: 1 })
			).then(res => {
				setProducts(res.variations);
				setLoading(false);
			});
		}
	}, [active]);

	return (
		<div className="news-recom row">
			<div className="col-12 title">
				<h3>{t(title)}</h3>
			</div>
			<div className="news col-md-2 col-12">
				<div className="row">
					{collection?.dynamicCollectionBlock?.map((item, i) => (
						<div className="col-md-6 col-3" key={i}>
							<div className="mini-card">
								<div className="header-card">
									<Banner
										item={item.photos[0]}
										smallSize={"150x_"}
									/>
								</div>
								<div className="body-card text-center">
									<h6>{item.name}</h6>
								</div>
							</div>
						</div>
					))}
				</div>
			</div>

			{menu.length > 0 ? (
				<div className="collection_menus">
					{menu.map(menu => (
						<div
							onClick={() => setActive(menu?.id)}
							className={`collection_menu ${
								active === menu?.id ? "active" : ""
							}`}
							key={menu?.id}
						>
							{menu?.name}
						</div>
					))}
				</div>
			) : null}

			<div className="recoms col-md-10 col-12">
				{!loading ? (
					<Splide
						className="slider"
						options={{
							...collectionFour,
							perPage: market.config.home === 5 ? 5.5 : 4.5,
							arrows: true
						}}
					>
						{products.map(product => (
							<SplideSlide
								key={product.variation.id}
								className="product-item"
							>
								<ProductCard
									product={product}
									type="sidebar"
									onAddToCart={dataProduct => {
										onAddToCart(dataProduct);
									}}
								/>
							</SplideSlide>
						))}
					</Splide>
				) : (
					<ShimmerProducts
						columns={10}
						option={{
							...collectionFour,
							perPage: market.config.home === 5 ? 5.5 : 4.5,
							arrows: false
						}}
					/>
				)}
			</div>
			<ChooseVariationModal
				setModal={setModal}
				modal={modal}
				addToCart={addToCart}
			/>
		</div>
	);
}
